import { useCallback, useEffect, useRef, useState } from 'react';
import './App.css';
import Calendar from "js-year-calendar";
import "js-year-calendar/dist/js-year-calendar.css";
import ContentComponent from './Components/ContentComponent';
import { DAL } from './DAL/dal'


function App() {

  const sender = new DAL()
  const ref = useRef()
  const [calendarHeight, setCalendarHeight] = useState(0)
  const [currentDate, setCurrentDate] = useState(new Date().toLocaleDateString())
  const [openCalendar, setOpenCalendar] = useState(false)
  const [state, setState] = useState({
    text: '',
    rowsCounter: 1,
    oneRowLength: 0
  })

  useEffect(() => {
    sender.getCurrentEntry(new Date().toLocaleDateString()).then(r => setState((state) => ({ ...state, text: r.description }))) // получаем запись на текущую дату
  }, [])

  useEffect(() => {
    const dateArray = []
    sender.getAllEntryDate().then(r => {
      for (let i in r) {
        dateArray.push(r[i].date)
      }
      const calendar = new Calendar('.calendar', {
        clickDay: function (e) {
          sender.getCurrentEntry(new Date(e.date).toLocaleDateString()).then(r => { // делает 2 запроса
            if (!r && r.date != new Date().toLocaleDateString()) return alert('На указанную дату нет записей')
            setState((state) => ({ ...state, text: r.description }))
            setCurrentDate(r.date)
          })
        },
        dataSource:
          dateArray.map(el => {
            if (el)
              return {
                startDate: new Date(el.split('.').reverse()),
                endDate: new Date(el.split('.').reverse())
              }
          })
      });
      calendar.setStyle("background");
      calendar.setMaxDate(new Date());
      setCalendarHeight(ref.current.clientHeight)
    })
  }, []);


  const openClose = useCallback(() => setOpenCalendar(!openCalendar), [openCalendar])
  const sendNewEntry = useCallback(() => sender.createNewEntry(currentDate, state.text)
    .then(r => alert('Запись добавлена'))
    .catch(err => console.log(err)), [state, currentDate])


  return (

    <div className='App'>
      <div className='time_line'>
        <div style={{ height: openCalendar ? calendarHeight : '50px', overflowY: openCalendar ? 'scroll' : 'hidden' }} className='calendar_wrapper'>
          <div ref={ref} className="calendar">
          </div>
        </div>
        <div className='open_and_date'>
          <span className='open_close' onClick={openClose}>
            {openCalendar ? 'Закрыть' : 'Открыть'}
          </span>
          <div>{currentDate === new Date().toLocaleDateString() ? 'Сегодня' : currentDate}</div>
        </div>
      </div>
      <div className='body'>
        <ContentComponent
          state={state}
          setState={setState}
          sendNewEntry={sendNewEntry}
          openCalendar={openCalendar}
          currentDate={currentDate}
        />

      </div>
    </div>

  );
}

export default App;
