import axios from "axios";



export const URL = "https://patient.pep-server.ru/";
// export const URL = "http://localhost:5000/";

const instance = axios.create({
    baseURL: URL,
});


export class DAL {
    createNewEntry(date, description) {
        return instance.post('api/set-entry', { date, description }).then(response => response)
    }
    getAllEntryDate() {
        return instance.get('api/get-entry-date').then(response => response.data)
    }
    getCurrentEntry(date) {
        return instance.post('api/get-entry', { date }).then(response => response.data)
    }
}

