import React, { useEffect, useRef } from "react";
import styles from "./container.module.css";

const ContentComponent = ({
  state,
  setState,
  sendNewEntry,
  openCalendar,
  currentDate,
}) => {
  const areaRef = useRef();

  useEffect(() => {
    setState((state) => ({
      ...state,
      rowsCounter: Math.ceil(
        areaRef?.current?.scrollHeight / areaRef?.current?.clientHeight
      ),
    }));
  }, []);

  const typeText = (e) => {
    setState((state) => ({ ...state, text: e.target.value }));
  };

  document.addEventListener("DOMContentLoaded", function () {
    console.log(1);
  });

  return (
    <>
      <div
        style={{ height: openCalendar ? "40vh" : "82vh" }}
        className={styles.content_wrapper}
      >
        <textarea
          style={{ height: openCalendar ? "300px" : "70vh" }}
          ref={areaRef}
          name="text"
          value={state.text}
          onChange={(e) => typeText(e)}
          readOnly={
            currentDate != new Date().toLocaleDateString() ? true : false
          }
        ></textarea>
        <button onClick={sendNewEntry}>Сохранить</button>
      </div>
    </>
  );
};

export default ContentComponent;
